import Styles from './SharePreview.module.scss';
import classNames from 'classnames/bind';
import { Link } from 'react-router-dom';
import { FacebookIcon, DipIcon, RepostIcon, SendIcon, CopyIcon } from '~/components/Icons';

const cx = classNames.bind(Styles);
function SharePreview() {
    return (
        <div className={cx('wrapper')}>
            <Link class={cx('share-items')}>
                <span>
                    <RepostIcon />
                </span>
                <span className={cx('share-preview-text')}>ng li</span>
            </Link>

            <Link class={cx('share-items')}>
                <span>
                    <DipIcon />
                </span>
                <span className={cx('share-preview-text')}>Nhng </span>
            </Link>

            <Link class={cx('share-items')}>
                <span>
                    <SendIcon />
                </span>
                <span className={cx('share-preview-text')}>Gi n bn b </span>
            </Link>

            <Link class={cx('share-items')}>
                <span>
                    <FacebookIcon />
                </span>
                <span className={cx('share-preview-text')}>Chia s vi Facebook</span>
            </Link>
            <Link class={cx('share-items')}>
                <span>
                    <CopyIcon />
                </span>
                <span className={cx('share-preview-text')}> Sao chp lin kt</span>
            </Link>
        </div>
    );
}

export default SharePreview;
